}

/* If the screen size is 768 or less */
@media only screen and (max-width: 767px) {
}

.root {
    color: $darkgrey;
}
.header {
    flex-wrap: wrap;
    display: flex;
    flex-direction: row-reverse;
}
.button_footer {
    margin: 2rem 0 0 0;
    display: flex;
    justify-content: space-between;
}
.section_footer {
    display: flex;
    justify-content: space-between;
    margin: 1em 0em 0em 0em;
}
.save_draft {
    color: $navy;
    background-color: $pink;
}

.delete_project {
    color: $grey;
    background-color: $reddo;
    margin-left: 20px;
}

.project_title {
    font-size: 40px;
    line-height: 1;
    font-weight: 600;
    margin-top: 10px;
    cursor: pointer;
}

.title_help {
    font-size: 16px;
    line-height: 1.2;
    font-style: italic;
    margin-top: 10px;
}

.section {
    padding: 2rem 0rem;
}

.parent_form {
	justify-content: center;
	background-image: url("../images/projectBg.svg");
	background-repeat: no-repeat;
	background-position: top;
}

.project_bg1 {
	background-image: url("../images/projectBg1.svg");
	background-repeat: no-repeat;
	background-position: top;
}

.project_bg2 {
	background-image: url("../images/projectBg2.svg");
	background-repeat: no-repeat;
	background-position: top;
}

.project_bg3 {
	background-image: url("../images/projectBg3.svg");
	background-repeat: no-repeat;
	background-position: top;
}
.child_form {
    width: 60%;
}
.heading {
    width: 160px;
    height: 36px;
    font-size: 36px;
    line-height: 1;
    font-weight: 400;
    display: inline;
}
.section_question {
    font-size: 12px;
    line-height: 1.5;
    padding: 1em 0em;
}
.section_input {
    flex-direction: column;
    display: flex;
    padding: 1em 3%;
}
.section_input_row {
    display: flex;
    margin: 1em 3%;
}
.section_header {
    color: $darkgrey;
    font-size: 24px;
    display: flex;
    margin: 0 0 0 3%;
    padding: 1em 10% 0.5em 10px;
    border-bottom: 1px solid $midgrey;
}
.header_style {
    margin: 0 0 0 1em;
}
.input_link {
    width: 300px;
    margin: 0 0 0 1em;
}
.input_link_left {
    width: 300px;
    margin: 0 1em 0 0;
}
.input_situation {
    height: 5em;
}
.input_teamsize {
    display: flex;
}
.teamsize_button {
    min-width: 60px;
    margin-right: 10px;
}
.teamsize_selected {
    background-color: $yellow;
}
.budget_input {
    display: inline-flex;
}
.dropdown_currency {
    margin-right: 10px;
}
.icon_trash {
    &:hover {
        color: $reddo;
        cursor: pointer;
    }
    align-items: center;
    margin-left: 20px;
    display: inline-flex;
}
.button_text {
    margin-left: 5px;
}
.center {
    align-self: center;
}

.section_card {
    padding: 1rem;
    margin: 1rem 0;
    box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.25);
    background-color: white;
    border-radius: 25px;
}
.error {
    color: red;
}
.section_grid {
    display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 1em;
}

.section_left {
    grid-column: 1 / 2;
    display: flex;
	flex-direction: column;
	justify-content: center;
}
.section_right {
    grid-column: 2 / 3;
	display: flex;
    justify-content: center;
    flex-direction: column;
}
.section_link {
    display: flex;
    align-items: center;
    color: $darkblue;
    font-size: 12px;
    line-height: 2;
}

.link_text {
    font-weight: 400;
    font-style: italic;
    margin: 0 0 0 1em;
    flex-wrap: wrap;
}
.section_desc {
    margin: 1em 0.5em;
}
