@import "variable";

.inp_text {
	font-size: 12px;
	font-family: "Font Awesome 5 Free", $font;
	border-radius: 10px;
	padding: 5px 10px;
	height: 36px;
	width: 200px;
	background: white;
	border: 1px solid rgba(0, 0, 0, 0.1);
	&:focus,
	&:hover {
		outline: none;
		border: 1px solid $midgrey;
		transition: border ease 0.3s;
	}
}

.tab_div {
	width: 940px;
}

.tab_list {
	margin: 0;
}

.tab_panel {
	display: flex;
	flex-direction: row;
	justify-content: center;
	column-gap: 20px;
	padding: 20px 0;
	border-radius: 0 15px 15px 15px;
	background: $ivory;
}
.transparent {
	background: transparent;
}

.tab {
	background: $lightgrey;
	padding: 8px 20px;
	color: $midgrey;
	cursor: pointer;
	border-radius: 8px 8px 0 0;
	transition: background ease 0.4s;
	&:hover {
		box-shadow: unset;
		transform: unset;
	}
}

.tab_active {
	background: $ivory;
	color: $darkgrey;
	transition: background ease 0.4s;
}

.role {
	margin: 0 8px 0 0;
	display: inline-flex;
	flex-direction: column;
	width: 120px;
	height: 55px;
	color: $midgrey;
}

.role span {
	font-weight: 600;
	font-size: 15px;
}

.role_active {
	background: $orange;
	color: $darkyellow;
	transition: background ease 0.6s;
}

@media screen and (max-width: 991px) {
	.tab_div {
		width: 100%;
	}
	.tab_panel {
		flex-direction: column;
		row-gap: 10px;
	}
	.tab_panel > * {
		width: 80%;
		margin: 0 auto;
	}
	.user_dropdown, .proj_dropdown {
		width: 100%;
	}
}