@import 'variable';

.container {
	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}

.content {
	width: 90%;
	flex-grow: 1;
	margin: 63px 83px 0px 83px;
	padding: 27px 24px 27px 24px;
	overflow: visible;
	align-self: center;
}

