.content.profile {
	background-color: $grey;
	box-shadow: 0px 0px 40px 0px $shadow;
	border-radius: 700px 700px 700px 50px;
}

.content.projects {
	background-color: $grey;
	box-shadow: 0px 0px 40px 0px $shadow;
	border-radius: 700px 50px 50px 50px;
}

.content.create {
	background-color: $grey;
	box-shadow: 0px 0px 40px 0px $shadow;
	border-radius: 50px 700px 50px 50px;
}

@media screen and (max-width: 991px) {
	.container {
		width: 100vw;
	}
	.content {
		width: 100vw;
		padding: 0px;
		margin: 0px;
		overflow: hidden;
	}
}


