@import '../modules/_button.module.scss';

.inp-text,
#date {
	font-size: 12px;
	font-family: 'Font Awesome 5 Free', $font;
	border-radius: 10px;
	border: 1px solid $ivory;
	background: $ivory;
	padding: 5px 10px;
	width: 250px;
	height: 36px;
	&:focus,
	&:hover {
		outline: none;
		border: 1px solid $midgrey;
		transition: border ease 0.3s;
	}
}
.inp-text-area,
#date {
	font-size: 12px;
	font-family: 'Font Awesome 5 Free', $font;
	border-radius: 10px;
	border: 1px solid $ivory;
	background: $ivory;
	padding: 5px 10px;
	max-height: 160px;
	resize: none;
	&:focus,
	&:hover {
		outline: none;
		border: 1px solid $midgrey;
		transition: border ease 0.3s;
	}
}

.inp-field {
	color: hsl(0, 0%, 20%);
	border: 1px solid $lightgrey;
	border-radius: 10px;
	background-color: white;
	padding: 5px 10px;
	min-width: 60px;
	::placeholder {
		color: #aaa;
	}
	&:focus,
	&:hover {
		outline: none;
		border: 1px solid $midgrey;
		transition: border ease 0.3s;
	}
}

.search {
	width: 40%;
	background-color: white;
	border: 1px solid rgba(0, 0, 0, 0.1);
}

.inp-text.disabled {
	background-color: $lightgrey;
}

#date {
	width: 200px;
}

#login {
	width: 250px;
	margin-top: 20px;
	font-size: 12px;
}

#google {
	font-size: 12px;
	display: flex;
	width: 250px;
	color: white;
	background: #ea4335;
}

#google i {
	font-size: 16px;
}

::-webkit-scrollbar {
	display: block;
	overflow: auto;
	width: 5px;
	height: 8px;
	border-radius: 5px;
}
/* Track */
::-webkit-scrollbar-track {
	background: $grey;
	border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: $midgrey;
	border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: $darkgrey;
}

html {
